<template>
  <div class="pa-5 pb-0">
    <Filters
      :filters="[
        { filterName: 'date', filterType: 'date', labelText: '' },
        {
          filterName: 'drl',
          filterType: 'select',
          labelText: '  ',
          isRemote: true,
          filterUrl: '/arm-isujt/server/drl.json'
        }
      ]"
      @reloadTable="updateTable()"
    />
    <Buttons :params="params" :exportUrl="createExportUrl" />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
      ref="table"
      :tableType="tableType"
      :hasDetail="true"
      @loadDetail="setDetail"
    />
    <DetailPanel :headers="detailHeaders" ref="detailPanel" />
  </div>
</template>

<script>
